import React from 'react'

export default function RelatedVehicles(props){
    let vehiclesList = props.vehicles.map(function(elem,index){
        return <div key={index}>{elem}</div>  
    })
    return(
        <div className="prop">
            <div className="title">Vehicles:</div>
            <div className="desc">{vehiclesList}</div>
        </div>
    )
}