import React from 'react'

export default function RelatedStarships(props){
    let starshipsList = props.starships.map(function(elem,index){
        return <div key={index}>{elem}</div>  
    })
    return(
        <div className="prop">
            <div className="title">Related starships: </div>
            <div className="desc">{starshipsList}</div>
        </div>
    )
}