import React, {Component} from "react"
import Loader from "./Loader"
import GeneralInfo from "./GeneralInfo"
import RelatedFilms from "./RelatedFilms"
import RelatedStarships from "./RelatedStarships"
import RelatedHomeworld from "./RelatedHomeworld"
import RelatedVehicles from "./RelatedVehicles"
import {Link} from "react-router-dom"
import {fetchPeoplesDetails,fetchPeoples} from '../services/fetchData'
import returnDiff from '../services/returnDiff'

class PeopleDetails extends Component{
    constructor(props){
        super(props);
        this.state={
            id:props.match.params.id,
            details:{},
            films:[],
            vehicles:[],
            starships:[],
            homeworld:'',
            loadDetails:false,
            maxCharId:0,
        }
    }
    loadData=(id)=>{
            Promise.all([
                fetchPeoples(), 
                fetchPeoplesDetails(id)
            ])
            .then(values=>{
                this.setState({
                    maxCharId:values[0].length+1,
                    details:values[1].data,
                    loadDetails:true,
                    films:values[1].filmsArr,
                    starships:values[1].starshipsArr,
                    vehicles:values[1].vehiclesArr,
                    homeworld:values[1].homeworld,
                })
            });
    }
    prevNextHandler=(id)=>{
        var self = this;
        this.setState({loadDetails:false,id:id},()=>{
            if(id==0){
                id=this.state.maxCharId;
            }else if(id==this.state.maxCharId){
                id=1;
            }
            console.log("id after cond",id)
           self.loadData(id);
        })
    }
    componentDidMount(){
        window.onpopstate = this.onBackButtonEvent;
        console.log("stateid",this.state.id)
        this.loadData(this.state.id);
       
    }
    onBackButtonEvent=()=>{
        this.prevNextHandler(
            returnDiff(
                /*window.location.href,"http://localhost:3000/characters/"*/
                window.location.href,"https://kamilgawron.github.io/characters"
            ));
    }
    componentWillUnmount(){
        window.onpopstate = () => {}
    }
    
    render(){
        console.log(this.state.loadDetails)
        if(this.state.loadDetails){
           return(
               <div className="character">
                   <h2>{this.state.details.name}</h2>
                   
                   <div className="content">
                       <div className="left">
                          <GeneralInfo 
                              gender={this.state.details.gender}
                              birthYear = {this.state.details.birth_year}
                              hairColor = {this.state.details.hair_color}
                              mass = {this.state.details.mass}
                              height = {this.state.details.height}
                              eyeColor = {this.state.details.eye_color}
                              skinColor = {this.state.details.skin_color}
                          />
                       </div>
                       <div className="right">
                            {this.state.films[0]?
                               <RelatedFilms
                                   films = {this.state.films}
                                />
                                : ""
                            }
                            {this.state.starships[0]?
                               <RelatedStarships
                                   starships = {this.state.starships}
                               />
                               : ""
                            }
                            {this.state.homeworld!=''?
                               <RelatedHomeworld
                                   homeworld = {this.state.homeworld}
                               />
                               : ""
                            }
                            {this.state.vehicles[0]?
                               <RelatedVehicles
                                   vehicles = {this.state.vehicles}
                               />
                               : ""
                            }
                       </div>
                   </div>
                   <div className="buttons">
                       <Link to={`../${this.state.id!=1? parseInt(this.state.id)-1 : this.state.maxCharId}/`}>
                           <div onClick={this.prevNextHandler.bind(this,(parseInt(this.state.id)-1))} className="prev">PREV</div>
                       </Link>
                       <Link to={`../${this.state.id!=this.state.maxCharId? parseInt(this.state.id)+1 : 0}/`}>
                           <div onClick={this.prevNextHandler.bind(this,(parseInt(this.state.id)+1))} className="next">NEXT</div>
                       </Link>
                   </div>
                   <div className="preparation">This page is still under preparation.</div>
               </div>
              )
        }
        else{
           return(
            <Loader />
          )
        }
       
    
    }
}
export default PeopleDetails