import returnDiff from './returnDiff'

export function fetchPeoples() {
    return new Promise(function (resolve, reject) {
        /*fetch(`https://cors-anywhere.herokuapp.com/https://swapi.co/api/people?page=1`)*/
        fetch(`https://swapi.co/api/people?page=1`)
            .then(response => response.json())
            .then(function (data) {
                let maxPage = Math.ceil(data.count / 10);
                return maxPage;
            })
            .then((maxPage) => {
                let tmpPeoples = [];
                let resultData = [];
                for (let i = 1; i <= maxPage; i++) {
                    /*fetch(`https://cors-anywhere.herokuapp.com/https://swapi.co/api/people?page=${i}`)*/
                    fetch(`https://swapi.co/api/people?page=${i}`)
                        .then(response => response.json())
                        .then(data => data.results)
                        .then(data => {
                            tmpPeoples = [...tmpPeoples, ...data]
                            return tmpPeoples
                        })
                        .then(data => {
                            let tmp = data.map(function (elem) {
                                elem.id = returnDiff(elem.url,"https://swapi.co/api/people/");
                                return elem;
                            })
                            return tmp;
                        })
                        .then(function (data) {
                            resultData = data;
                            if (i == maxPage) {
                                resolve(resultData)
                            }
                        })
                        .catch(err=>console.log("Err in pages",err))
                }

            })
        .catch(err=>console.log("err in first page",err))

    })
}
function fetchHomewrold(homeworldUrl){
    return new Promise(function(resolve,reject){
        if (homeworldUrl){
            let homeworlds='home1'
            fetch(homeworldUrl)
                .then(response=>response.json())
                .then(data=>{
                console.log("resolve homeworld promise");
                resolve(data.name)
            })
                .catch(err=>console.log("err in fetch homeworld",err))
        }
        else{
            resolve('')
        }
        
    })
}
function fetchVehicles(vehiclesArr){
    return new Promise(function(resolve,reject){
        let vehicles=[];
        if(vehiclesArr){
            if (vehiclesArr.length>0){
                for(let i=0;i<vehiclesArr.length;i++){
                    fetch(`${vehiclesArr[i]}`)
                        .then(response=>response.json())
                        .then(data=>vehicles=[...vehicles,data.name])
                        .then(()=>{
                        if(vehiclesArr.length==vehicles.length){
                            console.log("resolve vehicles promise");
                            resolve (vehicles)
                        }
                    })
                        .catch(err=>console.log("err in fetch vehicles",err))
                }
            }else{
                console.log("resolve vehicles promise");
                resolve([])
            }
        }else{
            resolve([])
        }
        
        
    })
}
function fetchStarships(starshipsArr){
    return new Promise(function(resolve,reject){
        let starships=[];
        if(starshipsArr){
            if(starshipsArr.length>0){
                for(let i=0;i<starshipsArr.length;i++){
                    console.log("in for loop starships promise");
                    fetch(`${starshipsArr[i]}`)
                        .then(response=>response.json())
                        .then(data=>starships=[...starships,data.name])
                        .then(()=>{
                        if(starshipsArr.length==starships.length){
                            console.log("resolve starships promise");
                            resolve (starships)
                        }
                    })
                        .catch(err=>console.log("err in fetch starships",err))
                }
            }else{
                console.log("resolve starships promise");
                resolve([]);
            }
        }else{
            resolve([])
        }
       
        
    })
}
function fetchFilmsTitles(filmsArr){
    return new Promise(function(resolve,reject){
        let films=[];
        if(filmsArr){
            if (filmsArr.length>0){
                for(let i=0;i<filmsArr.length;i++){
                    fetch(`${filmsArr[i]}`)
                        .then(response=>response.json())
                        .then(data=>films=[...films,data.title])
                        .then(()=>{
                        if(filmsArr.length==films.length){
                            console.log("resolve films promise");
                            resolve (films)
                        }
                    })
                        .catch(err=>console.log("err in fetch films",err))
                }
            }else{
                console.log("resolve films promise");
                resolve([]);
            }
        }
        else{
            resolve([])
        }
        
        
    })
}

export function fetchPeoplesDetails(id){
    return new Promise(function(resolve,reject){
        console.log("in promise");
        let loadFilms=false;
        /*fetch(`https://cors-anywhere.herokuapp.com/https://swapi.co/api/people/${id}`)*/
        fetch(`https://swapi.co/api/people/${id}`)
            .then(response=>response.json())
            .then((data)=>{
                Promise.all([
                    fetchFilmsTitles(data.films),
                    fetchHomewrold(data.homeworld),
                    fetchStarships(data.starships),
                    fetchVehicles(data.vehicles)
                ])
                .then(values=>{
                    let filmsArr = values[0];
                    let homeworld = values[1];
                    let starshipsArr = values[2];
                    let vehiclesArr = values[3];
                    resolve({
                        data,
                        filmsArr,
                        homeworld,
                        starshipsArr,
                        vehiclesArr
                    })
                })
            })
    })
}