import React from 'react'

export default function GeneralInfo(props){
    const {gender,birthYear,hairColor,mass,height,eyeColor,skinColor} = props;
    return(
        <React.Fragment>
            <div className="prop">
                <div className="title">Gender:</div>
                <div className="desc">{gender}</div>
            </div>
            <div className="prop">
                <div className="title">Birth year:</div>
                <div className="desc">{birthYear}</div>
            </div>
            <div className="prop">
                <div className="title">Hair color:</div>
                <div className="desc">{hairColor}</div>
            </div>
            <div className="prop">
                <div className="title">Mass:</div>
                <div className="desc">{mass}</div>
            </div>
            <div className="prop">
                <div className="title">Height:</div>
                <div className="desc">{height}</div>
            </div>
            <div className="prop">
                <div className="title">Eye color:</div>
                <div className="desc">{eyeColor}</div>
            </div>
            <div className="prop">
                <div className="title">Skin color:</div>
                <div className="desc">{skinColor}</div>
            </div>
        </React.Fragment>
    )
}